@import "../../main.scss";

.homeWrapper {
  position: absolute;
  background-color: $extraDarkColor;
  width: 100%;
  height: 100%;
  top: 0;
  transition: transform 0.3s ease-out;
}

.headerWrapper {
  position: absolute;
  width: 60%;
  height: 50%;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -65%);
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
  font-family: "Assistant ExtraLight";
  font-size: 66pt;
  text-align: center;
  color: $lightColor;
}

.image {
  position: absolute;
  top: 78pt;
  width: 100%;
  height: 80%;
  left: 50%;
  transform: translateX(-50%);
  mask-size: 100%;
  mask-position: center;
  mask-repeat: no-repeat;
  background-color: $lightColor;
}

.line {
  position: absolute;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  height: 2px;
  top: 76pt;
  background-color: $lightColor;
}

.tapButton {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 60px;
  width: 100px;
  height: 100px;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba($lightColor, 0.1);
  border: 1px solid $lightColor;
  border-radius: 50%;
}

.tapImage {
  position: relative;
  width: 80%;
  height: 80%;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
